#loadings {
  display: block;
  margin: auto;
  /* background-image: url("https://alpha-assets.stylori.com/images/static/loadingimg.gif"); */
  background-repeat: no-repeat;
  background-position: center;
}

.one-day-ship-listing-page {
  width: 24px;
  height: 22px;
  margin-left: 4px;
  margin-top: 28px;
  /* z-index: 2; */
  position: absolute;
  top: 15px;
  /* background-image: url('https://alpha-assets.stylori.com/images/static/product-details-icons.png'); */
  /* top: 5px; */
  background-position: -102px -37px;
  background-repeat: no-repeat;
  cursor: pointer;
  color: #ed1165 !important;
}

.one-day-ship-listing-page-withoutTop {
  width: 24px;
  height: 22px;
  margin-left: 4px;
  margin-top: 2px;
  /* z-index: 2; */
  position: absolute;
  top: 15px;
  /* background-image: url('https://alpha-assets.stylori.com/images/static/product-details-icons.png'); */
  /* top: 5px; */
  background-position: -102px -37px;
  background-repeat: no-repeat;
  cursor: pointer;
  color: #ed1165 !important;
}

.one-day-ship-listing-page-label {
  display: none;
}

.one-day-ship-listing-page-withoutTop-label {
  display: none;
}

.wishListStyle {
  margin-left: 4px;
  margin-top: 2px;
  z-index: 10;
  position: absolute;
  /* top: 15px; */
  /* left: 17px */
}

@media only screen and (max-width: 580px) and (min-width: 540px) {
  .one-day-ship-listing-page {
    width: 24px;
    height: 22px;
    margin-left: 4px;
    margin-top: 2px;
    /* z-index: 2; */
    position: absolute;
    top: 15px;
    left: 12px;
    /* background-image: url('https://alpha-assets.stylori.com/images/static/product-details-icons.png'); */
    /* top: 5px; */
    background-position: -102px -37px;
    color: #ed1165 !important;
    background-repeat: no-repeat;
    cursor: pointer;
  }
}

.wishListStyle i {
  font-size: 18px;
}

