.MuiCardActions-spacing > * + * {
  margin-left: 0px !important;
  width: 100%;
}

.one-day-ship-listing-page div {
  margin-left: 0px;
}

.one-day-ship-listing-page-label:hover {
  display: block;
  color: #f699a3;
  font-size: 11px;
}

.overall-loaders {
  display: flex;
}

@keyframes placeHolderShimmer {
  0% {
    background-position: -490px 0;
  }

  100% {
    background-position: 490px 0;
  }
}

.imgDiv {
  width: auto;
  height: auto;
  border: none;
}

.price {
  height: 20px;
  width: 100px;
}

.title {
  height: 40px;
  width: 100%;
  word-wrap: break-word;
  font-weight: bold !important;
}

.card-control {
  z-index: 2;
  position: absolute;
}

.card-control-topleft {
  top: 0px;
  left: 0px;
}

.card-control-bottomleft {
  bottom: 30%;
  left: 0px;
}

.card-control-topright {
  top: 0px;
  right: 0px;
}

.card-control-bottomright {
  bottom: 30%;
  right: 0px;
}

.productLayoutRoot {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  overflow: hidden;
  /* background-color: whitesmoke; */
  height: 100%;
  /* height: 100vh; */
}

.productLayoutRootFull {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  overflow: hidden;
  /* background-color: whitesmoke; */
  /* height: 100%; */
  height: 100vh;
  background-color: #f699a3;
}

.productLayoutGridList {
  width: 100%;
  height: auto;
  /* text-align: center; */
}
